
const initialState = {
    cart: [],
    inCart: 0
};
const cartReducer = (state = initialState, action) => {
    let carts = [...state.cart];
    let totalQty = 0;
    let item = [];
    let newCart = [];

    switch (action.type) {
        case 'ADD_TO_CART':
            item = carts.filter(item => item.Id == action.payload.Id);
            if (item != null && item != undefined && item.length > 0) {
                item.map(m => {
                    m.Qty += action.payload.Qty;
                });
            } else {
                carts.push(action.payload);
            }

            carts.map(m => totalQty += m.Qty);
            
            return {
                ...state,
                cart: carts,
                inCart: totalQty
            };
        case 'UPDATE_CART_QUANTITY':
            item = carts.find(item => item.Id == action.payload.Id);
            newCart = carts.filter(item => item.Id != action.payload.Id);
            item.Qty = action.payload.Qty;
            newCart.push(item);
            carts.map(m => totalQty += m.Qty);
            return {
                ...state,
                cart: newCart,
                inCart: totalQty
        };
        case 'REMOVE_FROM_CART':
            newCart = carts.filter(item => item.Id != action.payload.Id);
            newCart.map(m => totalQty += m.Qty);
            return {
                ...state,
                cart: newCart,
                inCart: totalQty
            };
        case 'REMOVE_ALL_CART':
            return {
                ...state,
                cart: newCart,
                inCart: 0
            };
        default:
            return state;
    }
};
export default cartReducer;