import React from "react";
import { connect } from "react-redux";
import Carousel from 'react-elastic-carousel';
import * as dealActions from "../../appState/actions/dealActions";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import Spinner from "../Common/Spinner";
import { Link } from "react-router-dom";
import AddToCart from "../Common/AddToCart";
import * as commonActions from "../../appState/actions/commonActions";
import './home.css';
class DealsPage extends React.Component {
  state = {
    redirectToAddCoursePage: false
  };

    componentDidMount() {
        
        const { dealsLasted, commons, actions } = this.props;

        if (dealsLasted.length === 0) {
            actions.getDealsLatest().catch(error => {
                alert("Loading deals failed" + error);
            });
        }

        if (commons != null && commons.length === 0) {
            actions.getCommons().catch(error => {
                alert("Loading commons failed" + error);
            });
        }

        this.breakPoints = [
            { width: 1, itemsToShow: 1 },
            { width: 550, itemsToShow: 2, itemsToScroll: 2, pagination: false },
            { width: 850, itemsToShow: 3 },
            { width: 1150, itemsToShow: 4, itemsToScroll: 2 },
            { width: 1450, itemsToShow: 5 },
            { width: 1750, itemsToShow: 6 },
        ]
    }

    
  render() {
    return (
      <>
        {this.props.loading ? (
          <Spinner />
        ) : (
                    <><div className="main-slider">
                        <Carousel itemsToShow={4} itemPadding={[5, 20]} breakPoints={this.breakPoints}>
                          
                            {this.props.dealsLasted.map(item =>
                                <div className="product-item" key={item.id}>
                                    <div className="pi-pic">
                                        <Link to={"/category/" + item.subCateName + "/" + item.subSubCateName + "/" + item.id}><img key={item.id + item.fileNameImg} src={`/home/img/product/${item.fileNameImg}`} /></Link>
                                        <div className="pi-links">
                                            <AddToCart key={item.id + item.subSubCateID} item={item} isSelected={true} />
                                            <a href="#" className="wishlist-btn"><i className="flaticon-heart"></i></a>
                                        </div>
                                    </div>
                                    <div className="pi-text">
                                        <h6>${item.currentPrice}</h6>
                                        <p>{item.dealName}</p>
                                    </div>
                                </div>
                            )}
                        </Carousel>
                    </div>    
          </>
        )}
      </>
    );
  }
}

DealsPage.propTypes = {
  dealsLasted: PropTypes.array.isRequired,
  actions: PropTypes.object.isRequired,
  commons: PropTypes.array.isRequired,
  loading: PropTypes.bool.isRequired
};

function mapStateToProps(state) {
  return {
      dealsLasted:
          state.dealsLasted.length === 0
        ? []
              : state.dealsLasted.map(deal => {
            return {
              ...deal 
            };
          }),
      commons:
          state.commons.length === 0
              ? []
              : state.commons.map(com => {
                  return {
                      ...com
                  };
              }),
    loading: state.apiCallsInProgress > 0
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: {
          getDealsLatest: bindActionCreators(dealActions.getDealsLatest, dispatch), 
          getCommons: bindActionCreators(commonActions.getCommons, dispatch)
    }
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(DealsPage);
