import React from 'react';
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import { Link } from "react-router-dom";
import * as categoryActions from "../../appState/actions/categoryActions";
import './NavMenu.css';
import SearchHome from './SearchHomePage';
import NavSubMenu from './NavSubMenu';

import { ToastContainer } from 'react-toastify';
import { authCustomer} from '../../_helpers';

class NavMenuPage extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            redirectToAddCoursePage: false,
            isDisabled: authCustomer() === null || "" ? true : false,
            
        }
        
    }
    componentDidMount() {

        const { categorys, actions } = this.props;

        if (categorys.length === 0) {
            actions.getCategories().catch(error => {
                alert("Loading categorys failed" + error);
            });
        }
    }

  render() {
        return (
            <><header className="header-section">
                <SearchHome key={10010} />
                <nav className="main-navbar">
                    <div className="container">
                        <ul key={1} className="main-menu">
                            <li key={11}><Link to="/home/">Home</Link></li>
                            {this.props.categorys.length > 0 ? this.props.categorys.map((item, index )=> 
                            (
                                    <><li key={item.id + "" + index}><Link key={item.id + "_" + index} to={"/category/" + item.categoryID}>{item.categoryName}</Link>
                                        {item.listSub != null && item.listSub.length > 0 ? <NavSubMenu key={item.categoryID} category={item.listSub} /> : ""}
                                    </li></>)): ""}
                            <li key={2}>
                                <a key={10011} href="#">Pages</a>
                                <ul key={3} className="sub-menu">
                                    <li key={4}><a href="./product.html">Product Page</a></li>
                                    <li key={5}><a href="./category.html">Category Page</a></li>
                                    <li key={6}><a href="./cart.html">Cart Page</a></li>
                                    <li key={7}><a href="./checkout.html">Checkout Page</a></li>
                                    <li key={8}><a href="./contact.html">Contact Page</a></li>
                                </ul>
                            </li>
                        </ul>
                        <ToastContainer position="bottom-right" 
                            autoClose={2000}/>
                    </div>
                </nav>
            </header>
            </>
        );
    }
    
}

NavMenuPage.propTypes = {
    categorys: PropTypes.array.isRequired,
    actions: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
    isDisabled: PropTypes.bool
};
function mapStateToProps(state) {
    return {
        categorys:
            state.categorys.length === 0
                ? []
                : state.categorys.categoryViewModel.map(cate => {
                    return {
                        ...cate
                    };
                }),
        isDisabled: authCustomer() === null || "" ? true : false,
        loading: state.apiCallsInProgress > 0
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: {
            getCategories: bindActionCreators(categoryActions.getCategories, dispatch)

        }
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(NavMenuPage);