import React from "react";
import PropTypes from "prop-types";

const Star = ({ selected = false, onClick = f => f }) => (
    <i className={selected ? "fa fa-star-o selected" : "fa fa-star-o fa-fade"} onClick={onClick}>
    </i>
);

Star.propTypes = {
    selected: PropTypes.bool,
    onClick: PropTypes.func
};

    const StarRating = ({
        totalStars,
        starsSelected,
        change,
        star
    }) => {
        starsSelected = starsSelected == 0 ? star : starsSelected; 

        return (
            
            <div className="star-rating">
                {[...Array(totalStars)].map((n, i) => (
                    <Star
                        key={i}
                        selected={i < starsSelected}
                        onClick={() => star = change(i + 1)}
                    />
                ))}
                <p ><input id="hdstar" name="hdstar" type="hidden" value={starsSelected} />
                    {starsSelected} of {totalStars} stars
        </p>
            </div>
        );
   }

StarRating.propTypes = {
    totalStars: PropTypes.number,
    starsSelected: PropTypes.number,
    starsSelect: PropTypes.number,
    change: PropTypes.func,
    star: PropTypes.number,
};

StarRating.defaultProps = {
    totalStars: 5,
    starsSelect: 5
};
export default StarRating;