import React from 'react';
import { connect } from 'react-redux';
import { Redirect } from 'react-router-dom';
import PropTypes from "prop-types";
import { ToastContainer, toast } from 'react-toastify';
import { bindActionCreators } from "redux";
import { login, logout } from '../../_actions/user.actions';
import HomePage from '../Home/HomePage';
import 'react-toastify/dist/ReactToastify.css';
class LoginPage extends React.Component {
    constructor(props) {
        super(props);

        // reset login status
        this.props.actions.logout();

        this.state = {
            username: '',
            password: '',
            submitted: false,
            authentication: {
                loggingIn: false,
                user: {}
            },
            login,
            logout,
            ...props
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(e) {
        const { name, value } = e.target;
        this.setState({ [name]: value });
    }

    handleSubmit(e) {
        e.preventDefault();

        this.setState({ submitted: true });
        const { username, password } = this.state;
        //const { addToast } = useToasts();
        if (username && password) {
            this.props.actions.login(username, password).then(user => {
                console.log(user);
                if (user === undefined || user === 'undefined' || user.name === "") {
                    toast("User name or password is incorrect!");
                    history.push("/Login/");
                    return false;
                } else {
                    toast("login successful!");
                    history.push("/home/");
                }
            }).catch(error => {
                //toast.success("Login failed!.");
                    console.log(error);
                });
        }
    }

    render() {
        const { loggingIn } = this.props;
        const { username, password, submitted } = this.state;
       
        return (
            <>

            {
                    loggingIn === true ? (
                        <><Redirect to="/home" /><HomePage /><ToastContainer/>
                         </>
                    )  : (
                            <><ToastContainer />
            <div className="container">
            <div className="col-md-6 col-md-offset-3">
                <h4>Login</h4>
                <form name="form" onSubmit={this.handleSubmit}>
                    <div className={'form-group' + (submitted && !username ? ' has-error' : '')}>
                        <label htmlFor="username">Username</label>
                        <input type="text" className="form-control" name="username" value={username} onChange={this.handleChange} />
                        {submitted && !username &&
                            <div className="help-block">Username is required</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !password ? ' has-error' : '')}>
                        <label htmlFor="password">Password</label>
                        <input type="password" className="form-control" name="password" value={password} onChange={this.handleChange} />
                        {submitted && !password &&
                            <div className="help-block">Password is required</div>
                        }
                    </div>
                    <div className="form-group">
                                                <button className="btn btn-primary">Login</button>
                                                {loggingIn &&
                            <img src="data:image/gif;base64," />
                        }
                       
                    </div>
                </form>
                </div>
                            </div>
                        </>
                    )
            }
			</>
        );
    }
}
LoginPage.propTypes = {
    logout: PropTypes.func,
    loggingIn: PropTypes.bool,
    login: PropTypes.func,
    actions: PropTypes.object,
};
function mapStateToProps(state) {
    //const { loggingIn } = state.authentication;
    return { loggingIn: state.authentication.loggedIn };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: {          
            login: bindActionCreators(login, dispatch),
            logout: bindActionCreators(logout, dispatch)
        }
    };
}

export default connect(
    mapStateToProps, mapDispatchToProps
)(LoginPage);