import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import $ from 'jquery';
import Spinner from "../Common/Spinner";
import * as dealActions from "../../appState/actions/dealActions";
import DealItem from './DealItem';

import './list.css';
class DealsList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            redirectToAddCoursePage: false,
            currentPage: 1,
            todosPerPage: 8,           
            ...props
        };
        
    }
    
    handleClick = this.handleClick.bind(this);
    
    componentDidMount() {

        const { dealsAll, actions } = this.props;

        if (dealsAll.length === 0) {
            actions.getDealsAll().catch(error => {
                alert("Loading deals failed" + error);
            });
        }
    }
     
      
    handleClick(event) {
        $(".pagination > li").removeClass("active");
        event.target.className = "page-link active";
        this.setState({
            currentPage: Number(event.target.id)
        });
    }
   
    render() {

        const { currentPage, todosPerPage } = this.state;
       
        const indexOfLastTodo = currentPage * todosPerPage;
        const indexOfFirstTodo = indexOfLastTodo - todosPerPage;
        const currentTodos = this.props.dealsAll.slice(indexOfFirstTodo, indexOfLastTodo);

        const pageNumbers = [];
        for (let i = 1; i <= Math.ceil(this.props.dealsAll.length / todosPerPage); i++) {
            pageNumbers.push(i);
        }

        const renderPageNumbers = pageNumbers.map(number => {
            return (
                <li className="page-link"
                    key={number}
                    id={number}
                    onClick={this.handleClick}
                >  {number}
                </li>
            );
        });

        return (
      <>
                {this.props.loading ? (
                    <Spinner />
                ) : (
                    <>
    <div className="product-filter-section">
        <div className="container">
            <div className="section-title">
                <h2>BROWSE TOP SELLING PRODUCTS</h2>
            </div>
            
                                    <div className="row">
                                        <DealItem dealItems={currentTodos}/>
                                        <ul className="pagination">
                                            {renderPageNumbers}
                                        </ul>
    </div>
                </div>
            </div>
                        </>
                    )}
            </>
        );
    }
}

DealsList.propTypes = {
    dealsAll: PropTypes.array.isRequired,
    actions: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
 
};

function mapStateToProps(state) {
     
    return {
        dealsAll:
            state.dealsAll.length === 0
                ? []
                : state.dealsAll.map(deal => {
                    return {
                        ...deal
                    };
                }),

        loading: state.apiCallsInProgress > 0,
       
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: {
            getDealsAll: bindActionCreators(dealActions.getDealsAll, dispatch)

        }
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DealsList);

