import React from "react";
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { Redirect, Link } from 'react-router-dom';
import { bindActionCreators } from "redux";
import { ToastContainer, toast } from 'react-toastify';
import { Total } from '../Category/CommonProperty';
import { placeorder, getById } from '../../_actions/user.actions';
import HomePage from '../Home/HomePage';
import { removeAllCart } from "../../appState/actions/cartActions";
import { updateTotalCart } from "../Common/AddToCart";
class CheckOutPage extends React.Component {
	
	constructor(props) {
		super(props);
		this.state = {
			order: {
				FirstName: props.user.UserName,
				LastName: props.user.UserName,
				Id: props.user.id,				
				Email: '',
				MobilePhone: '',
				Address: '',
				Address2: '',
				Cart: props.cart,
				ShipPrice: 0,
				ShipName: '',
				ShipType: '',
				SubTotal: 0
			}
			, users: {},
			submitted: false,
			removeAllCart,
			placeorder,
			getById,
			inCart: props.inCart,
			commons: props.commons,
			...props
		};
		this.handleChange = this.handleChange.bind(this);
		this.handleSubmit = this.handleSubmit.bind(this);
		this.handleClick = this.handleClick.bind(this);
		this.handleShipClick = this.handleShipClick.bind(this);
	}

	handleChange(event) {
		const { name, value } = event.target;
		let { order } = this.state;
		this.setState({
			order: {
				...order,
				[name]: value,
				Cart: this.state.cart
			}
		});
	}

	handleShipClick(event) {
		let { commons } = this.props;	
		let { order, cart } = this.state;
		let totalQty = 0;
		let ship = commons.filter(f => f.id === event.target.id);
		let shipDefault = commons.filter(f => f.commonID === 0 && f.type === "shipIn");
		let shipPrice = ship[0].commonID !== shipDefault[0].commonID ? (ship[0].rateOfExchange + shipDefault[0].rateOfExchange) / 2 : shipDefault[0].rateOfExchange;
		cart.map(s => totalQty += s.Price * s.Qty);
		
		this.setState({
			order: {
				...order,
				ShipPrice: shipPrice,
				ShipName: ship[0].name,
				ShipType: ship[0].id,
				SubTotal: totalQty
			}
		});
		
	}

	handleClick(event) {
		
		let { users, actions, user, cart } = this.props;
		let { order } = this.state;
		if (event.target.id === 'one') {
			if (users != null && users.items === undefined && user.id.length > 0 && cart.length > 0) {
				actions.getById(user.id).then(ret => {
					users = ret;
					this.setState({
						order: {
							Email: users.email, MobilePhone: users.mobilePhone, Address: users.companyAddress, Id: users.id
							, Cart: this.state.cart
						}
					});
				}).catch(err => {
					console.log(err);
				});
			} else {
				this.setState({
					order: {
						Email: users.items.email, MobilePhone: users.items.mobilePhone, Address: users.items.companyAddress, Id: users.items.id
						, Cart: this.state.cart
					}
				});
			}
		} else {
			this.setState({
				order: {
					...order,
					Cart: this.state.cart
				}
			});
		}
	}

	handleSubmit(event) {
		event.preventDefault();
		let { order, cart } = this.state;

		this.setState({
			order: {
				...order,
				Cart: cart
			}
		});
		if (this.state.order.Email && this.state.order.MobilePhone && this.state.order.Address) {
			
			this.props.actions.placeorder(order)
			.then(() => {
				toast("Order successful!");
				this.props.actions.removeAllCart();
				this.props.actions.updateTotalCart("0");

				history.push("/home/");
				
			}).catch(() => {
							
			});
			this.setState({
				Cart: [], submitted: true
			});
		}
	}

	render() {
		const { cart, submitted, commons, order } = this.state;
		
		return (
			<>
				{submitted === true || cart.length === 0 ? (
					<><Redirect to="/home" /><HomePage /><ToastContainer />
					</>
				): (
						<>
						<div className="page-top-info">
							<div className="container">
								<h4>CheckOut</h4>
								<div className="site-pagination">
									<Link to={"/home/shoppingcart/"}>Home/Your cart</Link>
								</div>
							</div>
						</div>
							<section className="product-section spad">
								<div className="container">
									<div className="row">
										<div className="col-lg-8 order-2 order-lg-1">
											<form className="checkout-form" onSubmit={this.handleSubmit}>
												<div className="cf-title">Billing Address</div>
												<div className="row">
													<div className="col-md-7">
														<p>*Billing Information</p>
													</div>
													<div className="col-md-5">
														<div className="cf-radio-btns address-rb">
															<div className="cfr-item">
																<input type="radio" name="pm" id="one" onClick={this.handleClick} />
																	<label htmlFor="one">Use my regular address</label>
									</div>
																<div className="cfr-item">
																<input type="radio" name="pm" id="two" onClick={this.handleClick}/>
																<label htmlFor="two">Use a different address</label>
									</div>
																</div>
															</div>
												</div>
												
															<div className="row address-inputs">															
													<div className="col-md-6">
														<div className={'form-group' + (submitted && !this.state.order != undefined  ? ' has-error' : '')}>
															<input type="text" placeholder="Email" name="Email" value={this.state.order.Email} onChange={this.handleChange} />
														</div>
													</div>
													<div className="col-md-6">
														<input type="text" placeholder="Phone no." name="MobilePhone" value={this.state.order.MobilePhone} onChange={this.handleChange}/>
													</div>													
													<div className="col-md-12">
														<input type="text" placeholder="Address" name="Address" value={this.state.order.Address} onChange={this.handleChange}/>
														<input type="text" placeholder="Address line 2" name="Address2" value={this.state.order.Address2} onChange={this.handleChange} />														
													</div>
												</div>
												<div className="cf-title">Delievery Info</div>
												<div className="row shipping-btns">
													{
														commons.map(c => 
														<>
															<div className="col-6" key={c.id + "-" + c.commonID}><h4>{c.name}</h4></div>
															<div className="col-6" key={c.id+"_"+0.5*0.01}>
															<div className="cf-radio-btns">
																		<div className="cfr-item"><input type="radio" name="shipping" id={c.id} onClick={this.handleShipClick}/>
																			<label htmlFor={c.id}>{c.rateOfExchange}</label></div>
															</div>
															</div></> )
													}
												</div>
												
												<div className="cf-title">Payment</div>
												<ul className="payment-list"><li>Pay when you get the package</li>
						</ul>
												<button className="site-btn submit-order-btn">Place Order</button>
											</form>
										</div>
												<div className="col-lg-4 order-1 order-lg-2">
													<div className="checkout-cart">
														<h3>Your Cart</h3>
													<ul className="product-list">
													{
														this.props.cart != null ?
															this.props.cart.map(c =>
																<li key={c.Id}>
																	<div className="pl-thumb"><img src={`/home/img/product/${c.Img}`} alt="" /></div>
																	<h6>{c.Name}</h6>
																	<p>${c.Qty * c.Price}</p>
																</li>
															) : <li>cart is empty</li>
															}
													</ul>
													<ul className="price-list">
													<li>Total<span>$<Total cart={this.props.cart} /></span></li>
													<li>Shipping/{order.ShipName}<span>{order.ShipPrice}</span></li>
													<li className="total">Total<span>${order.SubTotal !== undefined ? order.SubTotal + order.ShipPrice : <Total cart={this.props.cart} />}</span></li>
													</ul>
													</div>
												</div>
																						</div>
								</div>
							</section>
							
						</>
					)}
			</>
		);
	}
}
export function getShipByType(coms) {
	return coms.filter(f => f.type === "shipIn");// || f.type === "shipEx"
}
CheckOutPage.propTypes = {	
	cart: PropTypes.array,
	placeorder: PropTypes.func,
	actions: PropTypes.object,
	order: PropTypes.object,
	user: PropTypes.object,
	removeAllCart: PropTypes.func,
	getById: PropTypes.func,
	users: PropTypes.object,
	submitted: PropTypes.bool,
	updateTotalCart: PropTypes.func,
	inCart: PropTypes.number,
	commons: PropTypes.array,
};
function mapStateToProps(state) {

	return {
		cart: state.cartReducer.cart,
		user: state.authentication.user,
		users: state.users,
		order: state.order,
		inCart: updateTotalCart(state.cartReducer.inCart),
		submitted: state.cartReducer.cart.length === 0 ? true : false,
		commons: state.commons.length > 0 ? getShipByType(state.commons) : []
	};
}
function mapDispatchToProps(dispatch) {
	return {
		actions: {
			placeorder: bindActionCreators(placeorder, dispatch),
			removeAllCart: bindActionCreators(removeAllCart, dispatch),
			getById: bindActionCreators(getById, dispatch)
		}
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(CheckOutPage);