import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import  AddToCart from "../Common/AddToCart";

const DealItem = ({ dealItems }) => (
    <>
        {dealItems.map((item, index) => {
            return (
                <div className="col-lg-3 col-sm-4" key={item.id + index}>
                    <div className="product-item">
                        <div className="pi-pic">
                            <Link to={"/category/" + item.subCateName + "/" + item.subSubCateName + "/" + item.id}><img key={item.id + item.fileNameImg} src={`/home/img/product/${item.fileNameImg}`} /></Link>
                            <div className="pi-links">
                                <AddToCart key={item.id + index + 11} item={item} isSelected={true}/>
                                <a href="#" className="wishlist-btn"><i className="flaticon-heart"></i></a>
                            </div>
                        </div>
                        <div className="pi-text">
                            <h6>${item.currentPrice}</h6>
                            <p>{item.dealName}</p>
                        </div>
                    </div>
                </div>            );
        })}
    </>
);

DealItem.propTypes = {
    dealItems: PropTypes.array.isRequired
};

export default DealItem;
