export const addToCart = (deal) => {
    return {
        type: 'ADD_TO_CART',
        payload: {
            Id: deal.id,
            Qty: deal.qty,
            Price: deal.price,
            Name: deal.name,
            Img: deal.img
        }
    }
};
export const removeFromCart = (deal) => {
    return {
        type: 'REMOVE_FROM_CART',
        payload: {
            Id: deal.id
        }
    }
};
export const updateCartQuantity = (deal) => {
    return {
        type: 'UPDATE_CART_QUANTITY',
        payload: {
            Id: deal.id,
            Qty: deal.qty
        }
    }
};

