import { userConstants } from '../_constants';
import { userService } from '../_services';
import { alertActions } from './';
import { history } from '../_helpers';
import * as types from "../appState/actions/actionTypes";
import { beginApiCall, apiCallError } from "../appState/actions/apiStatusActions";
export const userActions = {
    login,
    logout,
    register,
    getAll,
    delete: _delete,
    placeorder,
    getById,
};

export function login(username, password) {
    return function (dispatch)  {
        //dispatch(request({ username }));
        dispatch(beginApiCall());
        return userService.login(username, password)
            .then(
                user => {
                    if (user != undefined && user.name != "error") {
                        history.push('/home');
                        dispatch(success(user));
                        dispatch(alertActions.success("success"));
                        return user;
                    } else {
                        history.push('/login');
                        dispatch(failure("Invalid"));
                        dispatch(alertActions.error("Invalid"));
                        return "underfind";
                    }
                    
                }
        ).catch(error => {
            dispatch(failure(error.toString()));
            dispatch(alertActions.error(error.toString()));
            dispatch(apiCallError(error));
            throw error;
        });
    };

   
}

//function request(user) {
//    return { type: userConstants.LOGIN_REQUEST, user }
//}

function success(user) {
    return { type: types.LOGIN_SUCCESS, user }
}

function failure(error) {
    return { type: userConstants.LOGIN_FAILURE, error }
}

export function logout() {
    userService.logout();
    return { type: userConstants.LOGOUT };
}

export function register(user) {
    return function (dispatch) {
        dispatch(beginApiCall());

        return  userService.register(user)
            .then(
                user => { 
                    dispatch(success(user));
                    history.push('/login');
                    dispatch(alertActions.success('Registration successful'));
                }
        ).catch(error => {
            dispatch(failure(error.toString()));
            dispatch(alertActions.error(error.toString()));
            throw error;
        });
    };

    //function request(user) { return { type: userConstants.REGISTER_REQUEST, user } }
    function success(user) { return { type: userConstants.REGISTER_SUCCESS, user } }
    function failure(error) { return { type: userConstants.REGISTER_FAILURE, error } }
}

export function placeorder(order) {
    return function (dispatch) {
        dispatch(beginApiCall());

        return userService.placeorder(order)
            .then(
                order => {
                    dispatch(success(order));
                    history.push('/home');
                    dispatch(alertActions.success('Order successful'));
                    //return order;
                }
            ).catch(error => {
                dispatch(failure(error.toString()));
                dispatch(alertActions.error(error.toString()));
                throw error;
            });
    };

    //function request(user) { return { type: userConstants.REGISTER_REQUEST, user } }
    function success(order) { return { type: userConstants.ORDER_SUCCESS, order } }
    function failure(error) { return { type: userConstants.ORDER_FAILURE, error } }
}
export function getAll(id) {
    return function (dispatch) {
        //dispatch(request());

        return  userService.getAll(id)
            .then(
                orders => {
                    dispatch(success(orders));

                    return orders;
                }).catch(error => {
                    dispatch(failure(error.toString()))
                    return error;
                });
    };

    //function request() { return { type: userConstants.GETALL_REQUEST } }
    function success(orders) { return { type: userConstants.GETALL_SUCCESS, orders } }
    function failure(error) { return { type: userConstants.GETALL_FAILURE, error } }
}

export function getById(id) {
    //return dispatch => {
        //dispatch(request());
    return function (dispatch) {
        return  userService.getById(id)
            .then(
                users => {
                    dispatch(success(users));
                
                    return users;
                }).catch (error => {
                    dispatch(failure(error.toString()))
                    return error;
                });
    };

    //function request() { return { type: userConstants.GETALL_REQUEST } }
    function success(users) { return { type: userConstants.GETID_SUCCESS, users } }
    function failure(error) { return { type: userConstants.GETID_FAILURE, error } }
}

// prefixed function name with underscore because delete is a reserved word in javascript
function _delete(id) {
    return dispatch => {
        dispatch(request(id));

        userService.delete(id)
            .then(
                user => dispatch(success(user.id)),
                error => dispatch(failure(id, error.toString()))
            );
    };

    function request(id) { return { type: userConstants.DELETE_REQUEST, id } }
    function success(id) { return { type: userConstants.DELETE_SUCCESS, id } }
    function failure(id, error) { return { type: userConstants.DELETE_FAILURE, id, error } }
}