import React from "react";
import PropTypes from "prop-types";
import { connect } from 'react-redux';
import { bindActionCreators } from "redux";
import $ from 'jquery';
import { addToCart} from "../../appState/actions/cartActions";
import { toast, ToastContainer } from 'react-toastify';

const Cart = ({ selected = false, onClick = f => f, labelButton = "ADD TO CART" }) => (
    <a className="add-card"><i className={selected ? "flaticon-bag" : "site-btn"} onClick={onClick}>
        <span>{labelButton}</span>
    </i></a>
);

Cart.propTypes = {
    selected: PropTypes.bool,
    onClick: PropTypes.func,
    labelButton: PropTypes.string
};


const UpdateCart = ({ item, onClick = f => f}) => {
    <div className="pro-qty"><span className="dec qtybtn" onClick={onClick}>-</span>
        <input id={item.id} type="text" value={item.qty} /><span className="inc qtybtn" onClick={onClick}>+</span>
    </div>   
}

UpdateCart.propTypes = {
    cart: PropTypes.array,
    inCart: PropTypes.number,
    updateCartQuantity: PropTypes.func
};


export function updateTotalCart(totalQty) {
    $(".shopping-card").find("span").text(totalQty)
}
class AddToCart extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            cart: props.cart,
            inCart: props.inCart,
            addToCart,
            isSelected: props.isSelected,
            ...props
        };

        this.handleAddCart = this.handleAddCart.bind(this);
    }

    handleAddCart(event) {
    event.preventDefault();
     
        let value = $(".pro-qty").find('input[name=qty]').val();
        value = this.props.isSelected ? 1 : value
        let deal = { id: this.props.item.id, qty: parseFloat(value), price: this.props.item.currentPrice, name: this.props.item.dealName, img: this.props.item.fileNameImg};
        this.props.actions.addToCart(deal);        
        toast("add to cart");
    }
    
    render() {
        return (
            <><Cart selected={this.props.isSelected} onClick={this.handleAddCart} labelButton={this.props.isSelected ? "ADD TO CART" : "SHOP NOW"} /><ToastContainer position="bottom-right"
                autoClose={2000} /></>
        );
    }
}

AddToCart.propTypes = {    
    isSelected: PropTypes.bool,
    valueOfButton: PropTypes.string,
    click: PropTypes.func,
    item: PropTypes.object,
    actions: PropTypes.object.isRequired,
    cart: PropTypes.array,
    inCart: PropTypes.number,
    addToCart: PropTypes.func
};

function mapState(state) {

    return {
        cart: state.cartReducer.cart,
        inCart: updateTotalCart(state.cartReducer.inCart)
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: {
            addToCart: bindActionCreators(addToCart, dispatch),
            //updateCartQuantity: bindActionCreators(updateCartQuantity, dispatch)
        }
    };
}

export default connect(mapState, mapDispatchToProps)(AddToCart);