import * as types from "../actions/actionTypes";
import initialState from "./initialState";

function dealAllByReducer(state = initialState.dealsAllBy, action) {
  switch (action.type) {
      case types.GET_DEAL_All_BY_SUCCESS:
          return action.dealsAllBy;

    default:
      return state;
  }
}

export default dealAllByReducer;