import React from "react";
import PropTypes from "prop-types";
import $ from 'jquery';

export function handleClicks(event) {
    $("input:radio[name='" + event.target.name + "']").removeAttr("checked");
    $(event.target).attr("checked", "checked");

}
export function handleStar(event) {
    $(".p-rating i").removeClass("active");
    $(".product-pic-zoom div")[1].style.backgroundImage = "url(" + $(event.currentTarget.dataset)[0].imgbigurl + ")";
    $(event.target).closest("div")[0].className = "pt active";
}

export const CommonProperty = ({ commons, type, keyClass}) => {
    return (
        <>
            {commons.filter(f => f.type === type).length > 0 ? commons.filter(f => f.type === type).map(com =>
                (<div key={com.id} className={keyClass + "-item"}>
                    <input type="radio" name={keyClass} id={com.name + "-" + type} onClick={handleClicks} />
                    <label className={keyClass + "-" + com.name} htmlFor={com.name + "-" + type}>{com.type !== "color" ? com.name : ""}</label>
                </div>)) : "One " + type}
             
    </>
        )
};

export const CommonPropertyByID = ({ commons, type, keyClass, deal }) => {
    return (
        <>
            {commons.filter(f => f.type === type && f.commonID == deal.dealID).length > 0 ? commons.filter(f => f.type === type && f.commonID == deal.dealID
            ).map(com =>
                (<div key={com.id} className={keyClass + "-item"}>
                    <input type="radio" name={keyClass} id={com.name + "-" + type} onClick={handleClicks} />
                    <label className={keyClass + "-" + com.name} htmlFor={com.name + "-" + type}>{com.type !== "color" ? com.name : ""}</label>
                </div>)
            ) : <div className="sc-item">{"One " + type}</div>}
        </>
    )
};
export const CommonStar = ({ ratings}) => {
    return (
        <>
            {ratings.map(r =>
                r.comments.map(m => {
                for (var i = 1; i <= m.star; i++) {
                    (<i key={m + "start"} className="fa fa-star-o" onClick={handleStar}> </i>)
                }
                if (m.star < 5) {
                    for (var j = m.star; j <= 5; j++) {
                       ( <i className="fa fa-star-o fa-fade"> </i>)
                    }
                }
                }) 
            )}
        </>
    )
};
export const Total = ({ cart }) => (
    <>
        {cart.reduce((sum, i) => (
            sum += i.Qty * i.Price
        ), 0)}
    </>
)
export const TotalOrder = ({ order }) => (
    <>
        {order.reduce((sum, i) => (
            sum += i.qty * i.price
        ), 0)}
    </>
)
CommonProperty.propTypes = {
    commons: PropTypes.array.isRequired,
    type: PropTypes.string.isRequired,
    keyClass: PropTypes.string.isRequired
};
CommonPropertyByID.propTypes = {
    commons: PropTypes.array.isRequired,
    type: PropTypes.string.isRequired,
    keyClass: PropTypes.string.isRequired,
    deal: PropTypes.object
};
CommonStar.propTypes = {
    ratings: PropTypes.array.isRequired,
}
Total.propTypes = {
    cart: PropTypes.cart
}
TotalOrder.propTypes = {
    order: PropTypes.order
}
export default CommonProperty;