import * as Types from "./actionTypes";
import * as dealApi from "../../api/dealApi";
import { beginApiCall, apiCallError } from "./apiStatusActions";

export function getDealsSuccess(dealsHome) {
    return { type: Types.GET_DEAL_LATEST_HOME_SUCCESS, dealsHome };
}

export function getDealsLastedSuccess(dealsLasted) {
    return { type: Types.GET_DEAL_LATEST_SUCCESS, dealsLasted };
}

export function getDealsAllSuccess(dealsAll) {
    return { type: Types.GET_DEAL_All_SUCCESS, dealsAll };
}

export function getDealsAllBySuccess(dealsAllBy) {
    return { type: Types.GET_DEAL_All_BY_SUCCESS, dealsAllBy };
}

export function getDealsLatest() {
  return function(dispatch) {
    dispatch(beginApiCall());
      return dealApi
          .getDealsLatest()
          .then(dealsLasted => {
              dispatch(getDealsLastedSuccess(dealsLasted));
      })
      .catch(error => {
        dispatch(apiCallError(error));
        throw error;
      });
  };
}

export function getDealsLatestHome() {
    return function (dispatch) {
        dispatch(beginApiCall());
        return dealApi
            .getDeals()
            .then(dealsHome => {
                dispatch(getDealsSuccess(dealsHome));
            })
            .catch(error => {
                dispatch(apiCallError(error));
                throw error;
            });
    };
}

