import * as types from "../actions/actionTypes";
import initialState from "./initialState";

export default function ratingReducer(state = initialState.ratings, action) {
  switch (action.type) {
    case types.GET_RATING_SUCCESS:
          return action.ratings;
      case types.GET_RATING_ByID_SUCCESS:
          return action.ratings;
      case types.SAVE_RATING_SUCCESS:
          return [...state, { ...action.rating }];
    default:
      return state;
  }
}
