import { handleResponse, handleError } from "./apiUtils";

const baseUrl = process.env.API_URL+"/api/Deal";
 
export function getDeals() {

    return fetch(baseUrl +"/GetDealHomeLatest/")
    .then(handleResponse)
    .catch(handleError);
}

export function getDealsLatest() {

    return fetch(baseUrl + "/GetDealLatest/")
        .then(handleResponse)
        .catch(handleError);
}

export function getDealsAll() {

    return fetch(baseUrl +"/GetDealAll/")
        .then(handleResponse)
        .catch(handleError);
}

export function getDealsBy() {

    return fetch(baseUrl + "/GetDealByCategoryId/")
        .then(handleResponse)
        .catch(handleError);
}