import React from 'react';
import { Link, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import { ToastContainer, toast } from 'react-toastify';
import { register} from '../../_actions/user.actions';
import LoginPage from '../Login/LoginPage';

class RegisterPage extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            user: {
                FirstName: '',
                LastName: '',
                UserName: '',
                Password: ''
            },
            submitted: false,
            registration: {
                registering: false
            },
            register,
             ...props
        };

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    handleChange(event) {
        const { name, value } = event.target;
        const { user } = this.state;
        this.setState({
            user: {
                ...user,
                [name]: value
            }
        });
    }

    handleSubmit(event) {
        event.preventDefault();

        this.setState({ submitted: true });
        const { user } = this.state;
        if (user.FirstName && user.LastName && user.UserName && user.Password) {
            this.props.actions.register(user).then((user) => {
                console.log(user);
                toast("Register is success!.");
                history.push("/Login/");
            }).catch(error => {
                toast("Register failed!.");
                console.log(error);
            });
        }
    }

    render() {
        const { registering } = this.props;
        const { user, submitted } = this.state;
        return (
            <>

                {
                    registering === true ? (
                        <><Redirect to="/login" /><LoginPage /><ToastContainer />
                        </>
                    ) : (
                            <><ToastContainer />
            <div className="container">
            <div className="col-md-6 col-md-offset-3">
                <h2>Register</h2>
                <form name="form" onSubmit={this.handleSubmit}>
                        <div className={'form-group' + (submitted && !user.FirstName ? ' has-error' : '')}>
                        <label htmlFor="firstName">First Name</label>
                            <input type="text" className="form-control" name="FirstName" value={user.FirstName} onChange={this.handleChange} />
                            {submitted && !user.FirstName &&
                            <div className="help-block">First Name is required</div>
                        }
                    </div>
                        <div className={'form-group' + (submitted && !user.LastName ? ' has-error' : '')}>
                        <label htmlFor="lastName">Last Name</label>
                            <input type="text" className="form-control" name="LastName" value={user.LastName} onChange={this.handleChange} />
                            {submitted && !user.LastName &&
                            <div className="help-block">Last Name is required</div>
                        }
                    </div>
                        <div className={'form-group' + (submitted && !user.UserName ? ' has-error' : '')}>
                        <label htmlFor="username">Username</label>
                            <input type="text" className="form-control" name="UserName" value={user.UserName} onChange={this.handleChange} />
                            {submitted && !user.UserName &&
                            <div className="help-block">Username is required</div>
                        }
                    </div>
                    <div className={'form-group' + (submitted && !user.Password ? ' has-error' : '')}>
                        <label htmlFor="password">Password</label>
                            <input type="password" className="form-control" name="Password" value={user.Password} onChange={this.handleChange} />
                        {submitted && !user.Password &&
                            <div className="help-block">Password is required</div>
                        }
                    </div>
                    <div className="form-group">
                        <button className="btn btn-primary">Register</button>
                        {registering &&
                            <img src="data:image/gif;base64," />
                        }
                        <Link to="/login" className="btn btn-link">Cancel</Link>
                    </div>
                </form>
                </div>
                                </div>
                            </>
                        )
                }
            </>
        );
    }
}
RegisterPage.propTypes = {
    register: PropTypes.func,
    registering: PropTypes.bool,
    actions: PropTypes.object,
};
function mapStateToProps(state) {
    
    return { registering: state.registration.registering };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: {
            register: bindActionCreators(register, dispatch)
        }
    };
}


//const connectedRegisterPage = connect(mapState, actionCreators)(RegisterPage);
export default connect(mapStateToProps, mapDispatchToProps)(RegisterPage);