import $ from 'jquery';
export function authHeader() {
    // return authorization header with jwt token
    let user = JSON.parse(localStorage.getItem('user'));

    if (user && user.token) {
        return { 'Authorization': 'Bearer ' + user.token };
    } else {
        return {};
    }
}

export function authCustomer() {
    // return authorization header with jwt token
    let user = JSON.parse(localStorage.getItem('user'));

    if (user && user.id) {
        handleLoginMenu(user.username);
        return  user.id;
    } else {
        return null;
    }
}

export function handleLoginMenu(user) {
    $(".UserNameAccount").text(user);
    $(".main-menu-account").text("Logout");
    $("li.login-menu").addClass("disabled");
}