//import config from 'config';
import { authHeader, handleLoginMenu } from '../_helpers';
import { handleResponse, handleError } from "../api/apiUtils";
import $, { error } from 'jquery';
import { useDispatch } from 'react-redux';
export const userService = {
    login,
    logout,
    register,
    getAll,
    getById,
    update,
    delete: _delete,
    placeorder
};
const baseUrl = process.env.API_URL;
async  function postData(url = '', data = {}) {
    // Default options are marked with *
   
    const response = await fetch(url, {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        dataType: 'json',
        mode: 'cors', // no-cors, *cors, same-origin
        cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        credentials: 'same-origin', // include, *same-origin, omit
        headers: {
            'Accept': 'application/json',
            'Content-Type': 'application/json',
           
            'Authorization': authHeader().Authorization, 
            // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        redirect: 'follow', // manual, *follow, error
        referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
        body: JSON.stringify(data) // body data type must match "Content-Type" header
    });
    return response.json(); // parses JSON response into native JavaScript objects
}

function login(username, password) {
    var User = {
        Username: username,
        Password: password 
    }
   
    return postData(`${baseUrl}/api/users/authenticate`, User)     
        .then(res => {
            console.log(res);
            // JSON data parsed by `data.json()` call
            if (res.username != undefined) {
                handleLoginMenu(res.username);
                // store user details and jwt token in local storage to keep user logged in between page refreshes
                localStorage.setItem('user', JSON.stringify(res));
                return res.username;
            } else {
                return Promise.reject(error);
            }
        }).catch(error => {
            return error;
        });
}

function logout() {
    // remove user from local storage to log user out
    localStorage.removeItem('user');
    $(".main-menu-account").text('Login');
    $(".UserNameAccount").text('');
}

function getAll(id) {
    const requestOptions = {
        method: 'GET',
        headers: authHeader()
    };

    return fetch(`${baseUrl}/api/Customer/GetOrdersByCustomerId/` + id, requestOptions)
        .then(handleResponse)
        .catch(handleError);
}

function getById(id) {
    
    return fetch(`${baseUrl}/api/Customer/GetCustomerById/`+ id)
        .then(handleResponse)
        .catch(handleError);
}

