import React from "react";
import { connect } from "react-redux";
import Carousel from 'react-elastic-carousel';
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import * as dealActions from "../../appState/actions/dealActions";
import Spinner from "../Common/Spinner";
import './home.css';
 
class HomeDealsPage extends React.Component {
  state = {
      redirectToAddCoursePage: false 
  }
   
    componentDidMount() {
        
        const { dealsHome, actions } = this.props;

        if (dealsHome.length === 0) {
          actions.getDealsLatestHome().catch(error => {
        alert("Loading courses failed" + error);
      });
    }
}

  render() {
    return (
      <>
        {this.props.loading ? (
          <Spinner />
        ) : (
                    <><div>
                        <Carousel enableAutoPlay={true} disableArrowsOnEnd={false} itemsToScroll={1}>
                            
                            {this.props.dealsHome.map(item => 
                               //require.context('../../styles/img/' + props.fileNameImg, true);{`/uploads/${img.img.path}` `url(../../styles/img/${item.fileNameImg})`
                                <div className="hs-item set-bg" key={item.id} style={{ backgroundImage: `url(/home/img/${item.fileNameImg})` }}>
                                    
                                    <div className="main-slider">
                                        <div className="row">
                                            <div className="col-xl-6 col-lg-7 text-white">
                                               
                                                <span>New Arrivals</span>
                                                <h2 className="title">denim jackets</h2>
                                                <p>Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Quis ipsum sus-pendisse ultrices gravida. Risus commodo viverra maecenas accumsan lacus vel facilisis. </p>
                                                <a href="#" className="site-btn sb-line">DISCOVER</a>
                                                <a href="#" className="site-btn sb-white">ADD TO CART</a>
                                            </div>
                                        </div>
                                        <div className="offer-card text-white">
                                            <span>from</span>
                                            <h2>$29</h2>
                                            <p>SHOP NOW</p>
                                        </div>
                                    </div>
                                </div>
                            )}

                        </Carousel>
                    </div>
                       
          </>
        )}
      </>
    );
  }
}

HomeDealsPage.propTypes = {
  dealsHome: PropTypes.array.isRequired,
  actions: PropTypes.object.isRequired,
  loading: PropTypes.bool.isRequired
};

function mapStateToProps(state) {
  return {
      dealsHome:
          state.dealsHome.length === 0
        ? []
              : state.dealsHome.map(deal => {
            return {
              ...deal 
            };
          }),
   
    loading: state.apiCallsInProgress > 0
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: {
          getDealsLatestHome: bindActionCreators(dealActions.getDealsLatestHome, dispatch) 
      
    }
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(HomeDealsPage);
