import { userConstants } from '../_constants';

export function order(state = {}, action) {
  switch (action.type) {
    case userConstants.ORDER_REQUEST:
          return { order: state.order };
      case userConstants.ORDER_SUCCESS:
          return { order: state.order };
      case userConstants.ORDER_FAILURE:
      return {};
    default:
      return state
  }
}