import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import Zoom from 'react-img-zoom';
import { connect } from 'react-redux';
import { bindActionCreators } from "redux";
import $ from 'jquery';

import CarouselDealItems from '../Home/CarouselDealItems';
import { CommonPropertyByID } from './CommonProperty';
import TabRating from '../Common/TabRating';
import StarRating from "../Common/StarRating";
import * as actionCreators from "../../appState/actions/ratingActions";
import AddToCart from "../Common/AddToCart";
import Spinner from "../Common/Spinner";
import './ProductDetails.css';
class ProductDetails extends React.Component {
	constructor(props) {
		super(props);

	}

	componentDidMount() {
		const { actions } = this.props;
		let { ratings } = this.props;

		if (ratings !== null && ratings.length === 0) {

			actions.getRatingsByID(this.props.details.dealID).catch(error => {
				alert("Loading Ratings failed" + error);
			});
		} else
			if (ratings.filter(f => f.id !== this.props.details.dealID).length === 1) {

				actions.getRatingsByID(this.props.details.dealID).catch(error => {
					alert("Loading Ratings failed" + error);
				});
			}
	}

	handleCateClicks(event) {
		$(".product-thumbs-track div").removeClass("active");
		$(".product-pic-zoom div")[1].style.backgroundImage = "url(" + $(event.currentTarget.dataset)[0].imgbigurl + ")";
		$(event.target).closest("div")[0].className = "pt active";
	}

	handleQtyInc(event) {
		var $button = $(event.target);
		var oldValue = $button.parent().find('input').val();
		if ($button.hasClass('inc')) {
			var newVal = parseFloat(oldValue) + 1;
		} else {
			// Don't allow decrementing below zero
			if (oldValue > 0) {
				newVal = parseFloat(oldValue) - 1;
			} else {
				newVal = 0;
			}
		}
		$button.parent().find('input').val(newVal);
	}

	render() {
		return (
			<>
				{this.props.details.length === 0 ? (
					<Spinner />
				) : (
						<>
							<section className="product-section spad">
								<div className="container">
									<div className="back-link">
										<Link to={"/category/" + this.props.details.subCateName}>Back to Category</Link>
									</div>
									<div className="row">
										<div className="col-lg-6">
											<div className="product-pic-zoom">
												<Zoom key={this.props.details.id} className="product-big-img" img={`/home/img/product/${this.props.details.fileNameImg}`} {...this.props}
													zoomScale={3} zoomPosition="original" zoomWidth="default"
													width={550}
													height={600} />

											</div>
											<div className="product-thumbs product-thumbs-details">
												<div className="product-thumbs-track">
													{this.props.details.libarys != null && this.props.details.libarys.length > 0 ?
														this.props.details.libarys.map(lib =>
															<div key={lib.id} className="pt" data-imgbigurl={`/home/img/product/${lib.fileName}`} onClick={this.handleCateClicks}>
																<img key={lib.id} src={`/home/img/product/${lib.fileName}`} />
															</div>
														) : ""}
												</div>
											</div>
										</div>
										<div className="col-lg-6 product-details">
											<h2 className="p-title">{this.props.details.dealName}</h2>
											<h3 className="p-price">${this.props.details.currentPrice}</h3>
											<h4 className="p-stock">Available:{this.props.details.qoh > 0 ? <span> In Stock</span> : <span> Out Stock</span>}</h4>
											<div className="p-rating" key={this.props.details.id}><StarRating totalStars={5} starsSelected={this.props.ratings != null && this.props.ratings.length && this.props.ratings.filter(f => f.id === this.props.details.dealID).length > 0 ? this.props.ratings.filter(f => f.id === this.props.details.dealID)[0].star : 0} star={0} />
											</div>
											<div className="fw-size-choose">
												<p>Color</p>
												<CommonPropertyByID commons={this.props.commons} type="color" keyClass="cs" deal={this.props.details != null ? this.props.details : null} />
											</div>
											<div className="fw-size-choose">
												<p>Size</p>
												<CommonPropertyByID commons={this.props.commons} type="size" keyClass="sc" deal={this.props.details != null ? this.props.details : null} />
											</div>
											<div className="quantity">
												<p>Quantity</p>

												<div className="pro-qty"><span className="dec qtybtn" onClick={this.handleQtyInc}>-</span>
													<input type="text" name="qty" value="1" /><span className="inc qtybtn" onClick={this.handleQtyInc}>+</span></div>

											</div>
											<AddToCart key={this.props.details.id + "_cartid"} item={this.props.details} isSelected={false} />
											
											<div className="p-review">

												<TabRating details={this.props.details} />
											</div>

										</div>
									</div>
								</div>
							</section>
							<section className="related-product-section">
								<div className="container">
									<div className="section-title">
										<h2>RELATED PRODUCTS</h2>
									</div>
									<div className="product-slider">
										<CarouselDealItems dealItems={this.props.detailsLasted} />
									</div>
								</div>
							</section>
						</>
					)}
			</>
		);
	}	
}

ProductDetails.propTypes = {
	getRatingsByID: PropTypes.func,
	details: PropTypes.object.isRequired,
	detailsLasted: PropTypes.array.isRequired,
	commons: PropTypes.array.isRequired,
	ratings: PropTypes.array,
	actions: PropTypes.object.isRequired,
	cart: PropTypes.array,
	inCart: PropTypes.number,
	addToCart: PropTypes.func
};

function mapState(state) {

	return {
		ratings: state.ratings
	};
}

function mapDispatchToProps(dispatch) {
	return {
		actions: {
			getRatingsByID: bindActionCreators(actionCreators.getRatingsByID, dispatch)
		}
	};
}

export default connect(mapState, mapDispatchToProps)(ProductDetails);