import * as React from 'react';
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import './SearchHome.css';

class SearchHome extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            categorys: props.categorys,
            dealsAll: props.dealsAll
        }
        this.handleChange = this.handleChange.bind(this);
     
    }

    handleChange(event) {
        const { name, value } = event.target;
        
        this.setState({
            [name]: value
        });
    }

    render() {
       
        return (
            <>
                <header className="header-section">
                    <div className="header-top">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-2 text-center text-lg-left">

                                    <a href="./index.html" className="site-logo">
                                        <img src="/home/styles/img/logo_sieuthimini_small.png" alt="" />
                                    </a>
                                </div>
                                <div className="col-xl-5 col-lg-5 header-search-form" >

                                    <input type="text" name="SeachName" placeholder="Search on sieuthimini ...." value={this.state.SeachName} onChange={this.handleChange} />
                                   
                                    <Link
                                        to={{
                                            pathname: "/category/:search",
                                            search: this.state.SeachName,
                                            hash: "",
                                            state: { fromDashboard: true }
                                        }}
                                    ><i className="flaticon-search"></i></Link>
                                </div>
                                <div className="col-xl-5 col-lg-5">
                                    <div className="user-panel">
                                        <div className="up-item">

                                            <ul className="main-menu-login main-menu">
                                                <li><i className="flaticon-profile"></i><span className="UserNameAccount"></span>
                                                    <ul key={465} className="sub-menu">
                                                        <li key={466}><Link to="/login" className="btn btn-link main-menu-account">Sign In</Link></li>
                                                        <li><Link to="/register" className="btn btn-link">Create Account</Link> </li>
                                                        <li key={467}><Link to="/managementorder" className="btn btn-link">Manage account</Link></li>

                                                    </ul>
                                                </li>


                                            </ul>
                                        </div>
                                        <div className="up-item">
                                            <div className="shopping-card">
                                                <i className="flaticon-bag"></i>
                                                <span>0</span>
                                            </div>
                                            <Link to="/shoppingcart" >Shopping Cart</Link>
                                        </div>

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </header>
            </>
        );
    }

}

SearchHome.propTypes = {
    categorys: PropTypes.array,
    dealsAll: PropTypes.array
};
function mapStateToProps(state) {
    return {
        categorys:
            state.categorys.length === 0
                ? []
                : state.categorys.categoryViewModel.map(cate => {
                    return {
                        ...cate
                    };
                }),
        dealsAll: state.dealsAll
    };
}
export default connect(
    mapStateToProps
)(SearchHome);