import { combineReducers } from "redux";
import dealsLasted from "./dealReducer";
import dealsHome from "./dealHomeReducer";
import dealsAll from "./dealAllReducer";
import dealsAllBy from "./dealAllByReducer";
import categorys from "./categoryReducer";
import commons from "./commonReducer";
import ratings from "./ratingReducer";
import cartReducer from "./cartReducer";
import apiCallsInProgress from "./apiStatusReducer";
import { authentication } from "../../_reducers/authentication.reducer";
import { registration } from "../../_reducers/registration.reducer";
import { order } from "../../_reducers/order.reducer";
import { users} from "../../_reducers/users.reducer";
const rootReducer = combineReducers({
dealsLasted,
dealsHome,
dealsAll,
dealsAllBy,
categorys,
commons,
ratings,
apiCallsInProgress,
authentication,
registration,
cartReducer,
order,
users
});

export default rootReducer;
