import { handleResponse, handleError } from "./apiUtils";
const baseUrl = process.env.API_URL + "/api/Rating";
import $ from 'jquery';

export function getRatings() {
    return fetch(baseUrl)
    .then(handleResponse)
    .catch(handleError);
}

export function getRatingsByID(id) {
    return fetch(baseUrl +"/GetRatingById/"+id)
        .then(handleResponse)
        .catch(handleError);
}

export async function saveRating(rating) {
    let data = {
        Comment: rating.comment,
        CustomerId: rating.customerId,
        Star: rating.star,
        DealID: rating.dealID,
    }
    let user = JSON.parse(localStorage.getItem('user'));
    // Default options are marked with *
    return await fetch(baseUrl + "/Rating", {
        method: 'POST', // *GET, POST, PUT, DELETE, etc.
        mode: 'no-cors', // no-cors, *cors, same-origin
        cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
        credentials: 'same-origin', // include, *same-origin, omit
        observe: "response",
        responseType: "blob",
        headers: {
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': 'Bearer ' + user.token
            // 'Content-Type': 'application/x-www-form-urlencoded',
        },
        redirect: 'follow', // manual, *follow, error
        referrerPolicy: 'no-referrer', // no-referrer, *no-referrer-when-downgrade, origin, origin-when-cross-origin, same-origin, strict-origin, strict-origin-when-cross-origin, unsafe-url
        body: JSON.stringify(data) // body data type must match "Content-Type" header
    }).then((response) => {
        return response.json = data;
       
    }).catch(handleError);
   // return response.json(); // parses JSON response into native JavaScript objects
}

