export function saveRatings(rating) {
    let Rating = {
        Id: "safasfafafafasfa",
        Comment: rating.comment,
        CustomerId: rating.customerId,
        Star: rating.star,
        DealID: rating.dealID,
        CommentDate: '2020-12-01'
    }
    
    return   $.ajax({
        type: "POST", url: baseUrl + "/Rating", data: Rating
        },
    ).done(function (res) {
        return res.json();
    })
}