import React from "react";
import PropTypes from "prop-types";
import { Link } from "react-router-dom";
import $ from 'jquery';
import { connect } from 'react-redux';
import { bindActionCreators } from "redux";
import { updateCartQuantity, removeFromCart } from "../../appState/actions/cartActions";
import CarouselDealItems from '../Home/CarouselDealItems';
import { updateTotalCart } from "../Common/AddToCart";
import { Total } from '../Category/CommonProperty';
import Spinner from "../Common/Spinner";
import { authCustomer } from '../../_helpers';
//import '../Category/ProductDetails.css';

class ShoppingCart extends React.Component {
	
	constructor(props) {
		super(props);
		this.state = {
			cart: props.cart,
			inCart: props.inCart,
			updateCartQuantity,
			removeFromCart,
			isDisabled: authCustomer() === null || "" ? true : false
			,
			...props
		};
		this.handleQtyInc = this.handleQtyInc.bind(this);
	}
	
	handleQtyInc(event) {
		var $button = $(event.target);
		var oldValue = $button.parent().find('input').val();
		if ($button.hasClass('inc')) {
			var newVal = parseFloat(oldValue) + 1;
		} else {
			// Don't allow decrementing below zero
			if (oldValue > 0) {
				newVal = parseFloat(oldValue) - 1;
			} else {
				newVal = 0;
			}
		}
		$button.parent().find('input').val(newVal);
		let deal = { id: $button.parent().find('input')[0].id, qty: parseFloat(newVal)};
		if (newVal === 0) {
			this.props.actions.removeFromCart(deal);
		} else {
			this.props.actions.updateCartQuantity(deal);
		}
		this.forceUpdate();
	}

	render() {
		return (
			<>
				{this.props.cart === 0 ? (
					<Spinner />
				) : (
						<><div className="page-top-info">
							<div className="container">
								<h4>Your Cart</h4>
								<div className="site-pagination">
									<Link to={"/home/shoppingcart/"}>Home/Your cart</Link>
								</div>
							</div>
						</div>
							<section className="product-section spad">
								<div className="container">
									
									<div className="row">
										<div className="col-lg-8">
											<div className="cart-table">
												<h3>Your Cart</h3>
												<div className="cart-table-warp">
													<table>
														<thead>
															<tr>
																<th className="product-th">Product</th>
																<th className="quy-th">Quantity</th>
																<th className="size-th">Size</th>
																<th className="total-th">Price</th>
															</tr>
														</thead>
														<tbody>
															{
																this.props.cart != null && this.props.cart.length > 0 ?
																this.props.cart.map(c => 
																	
																<tr key={c.Id }>
																<td className="product-col">
																		<img src={`/home/img/product/${c.Img}`} alt=""/>
																		<div className="pc-title">
																			<h4>{c.Name}</h4>
																			<p>${c.Price}</p>
																		</div></td>
																<td className="quy-col">
																	<div className="quantity">
																			<div className="pro-qty"><span className="dec qtybtn" onClick={this.handleQtyInc}>-</span>
																					<input id={c.Id} type="text" value={c.Qty} /><span className="inc qtybtn" onClick={this.handleQtyInc}>+</span>
																		</div>
																		</div>
																</td>
																<td className="size-col"><h4>Size M</h4></td>
																<td className="total-col"><h4>${c.Price * c.Qty}</h4></td>
																</tr>	
																)
																: <tr><td>Cart is empty</td></tr>
															}
														</tbody>
													</table>
												</div>
												<div className="total-cost">
													<h6>Total <span>$<Total cart={this.props.cart != null && this.props.cart != undefined ? this.props.cart : []} /></span></h6>
												</div>
											</div>
										</div>
											<div className="col-lg-4 card-right">
																	
											{this.props.cart.length > 0 ? <Link to={!this.props.isDisabled && this.props.cart.length > 0 ? "/checkout" : "/home"} className="site-btn" style={this.props.isDisabled ? { pointerEvents: 'none' } : { pointerEvents: 'auto' }} alt={this.props.isDisabled ? "Please, please log into your account" : "Proceed to checkout"}>Proceed to checkout</Link> : ""}
											<Link to={"/home"} className="site-btn sb-dark">Continue shopping</Link>
				</div>
									</div>
								</div>
							</section>
							<section className="related-product-section">
								<div className="container">
									<div className="section-title">
										<h2>Continue Shopping</h2>
									</div>
									<div className="product-slider">
										<CarouselDealItems dealItems={this.props.dealsLasted} />
									</div>
								</div>
							</section>
						</>
					)}
			</>
		);
	}
}

ShoppingCart.propTypes = {	
	dealsLasted: PropTypes.array.isRequired,
	actions: PropTypes.object.isRequired,
	cart: PropTypes.array,
	inCart: PropTypes.number,
	isDisabled: PropTypes.bool,
	updateCartQuantity: PropTypes.func,
	removeFromCart: PropTypes.func
};

function mapState(state) {

	return {
		
		dealsLasted: state.dealsLasted,
		cart: state.cartReducer.cart,
		inCart: updateTotalCart(state.cartReducer.inCart),
		isDisabled: authCustomer() === null || "" ? true : false
	};
}
function mapDispatchToProps(dispatch) {
	return {
		actions: {		
			updateCartQuantity: bindActionCreators(updateCartQuantity, dispatch),
			removeFromCart: bindActionCreators(removeFromCart, dispatch)
		}
	};
}
export default connect(mapState, mapDispatchToProps)(ShoppingCart);