import React from 'react';
import { Route, Switch, Redirect} from "react-router-dom";
import PropTypes from "prop-types";
import { connect } from 'react-redux';
import $ from 'jquery';
//import PropTypes from "prop-types";
import NavMenu from './components/Common/NavMenu';
import HomePage from './components/Home/HomePage';
import PageNotFound from "./PageNotFound";
import ShoppingCartPage from './components/ShoppingCart/ShoppingCartPage';
import CategoryPage from './components/Category/CategoryPage';
import CheckOut from './components/CheckOut/CheckOutPage';
import ManagementOrder from './components/ManagementOrder/ManagementOrderPage';

import { history } from './_helpers';
import { alertActions } from './_actions';
import { PrivateRoute } from './_components';
import LoginPage from './components/Login/LoginPage';
import  RegisterPage  from './components/Register/RegisterPage';

import './App.css';
window.jQuery = window.$ = $;

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            alert: {
                message: ''
            },
            ...this.props
        };
        history.listen((location, action) => {
            // clear alert on location change
            this.props.clearAlerts();
        });
    }

    render() {
        const { alert } = this.props;
       
        return (
            <div>
                {alert.message &&
                    <div className={`alert ${alert.type}`}>{alert.message}</div>
                }
                <NavMenu />
                
                <Switch history={history}>
                    <PrivateRoute exact path="/" component={HomePage} />
                    <Route exact path="/home" component={HomePage} />
                    <Route path="/category" to='/' exact />
                    <Route path="/category/:search=" component={CategoryPage}  exact />
                    <Route path="/category/:subCateName?/:subSubCateName?/:details?" component={CategoryPage} exact />
                    <Route path="/login" component={LoginPage}  />
                    <Route path="/register" component={RegisterPage} />
                    <Route path="/shoppingcart" component={ShoppingCartPage} />
                    <Route path="/managementorder" component={ManagementOrder} />
                    <Route path="/checkout" component={CheckOut} />
                        <Redirect from="*" to="/home" />
                        <Route component={PageNotFound} />
                    </Switch>
                   
            </div>
        );
    }
}
App.propTypes = {
    clearAlerts: PropTypes.func,
    alert: PropTypes.object
};
function mapState(state) {
    return { alert: state };
}

const actionCreators = {
    clearAlerts: alertActions.clear
};

export default connect(mapState, actionCreators)(App);