import React from "react";
import { connect } from 'react-redux';
import PropTypes from "prop-types";
import { Redirect, Link } from 'react-router-dom';
import { bindActionCreators } from "redux";
import { ToastContainer, toast } from 'react-toastify';
import { Total, TotalOrder } from '../Category/CommonProperty';
import { placeorder, getById, getAll } from '../../_actions/user.actions';
import HomePage from '../Home/HomePage';
import { removeAllCart } from "../../appState/actions/cartActions";
import { updateTotalCart } from "../Common/AddToCart";

class ManagementOrderPage extends React.Component {
	
	constructor(props) {
		super(props);
		this.state = {
			order: {
				FirstName: props.user.UserName,
				LastName: props.user.UserName,
				Id: props.user.id,				
				Email: '',
				MobilePhone: '',
				Address: '',
				Address2: '',
				Cart: props.cart,
				ShipPrice: 0,
				ShipName: '',
				ShipType: '',
				SubTotal: 0
			}
			, users: {}, orders: [], ordersDetails: [],
			submitted: false,
			removeAllCart,
			placeorder,
			getById, getAll,
			inCart: props.inCart,
			commons: props.commons,
			...props
		};
		//this.handleChange = this.handleChange.bind(this);
		//this.handleSubmit = this.handleSubmit.bind(this);
		this.handleClick = this.handleClick.bind(this);
		//this.handleShipClick = this.handleShipClick.bind(this);

		
	}

	componentDidMount() {
		let { users, orders } = this.props;
		const {actions, user } = this.props;
		if (users != null && users.items === undefined && user.id.length) {
			actions.getById(user.id).then(ret => {
				users = ret;
				this.setState({
					order: {
						Email: users.email, MobilePhone: users.mobilePhone, Address: users.companyAddress, Id: users.id
						, Cart: this.state.cart
					}
				});

				actions.getAll(users.id).then(ret => {
					
					this.setState({
						orders: ret
					});
				}).catch(err => {
					console.log(err);
				});
			}).catch(err => {
				console.log(err);
			});

			
		}
	}
	handleChange(event) {
		const { name, value } = event.target;
		let { order } = this.state;
		this.setState({
			order: {
				...order,
				[name]: value,
				Cart: this.state.cart
			}
		});
	}

	handleShipClick(event) {
		let { commons } = this.props;	
		let { order, cart } = this.state;
		let totalQty = 0;
		let ship = commons.filter(f => f.id === event.target.id);
		let shipDefault = commons.filter(f => f.commonID === 0 && f.type === "shipIn");
		let shipPrice = ship[0].commonID !== shipDefault[0].commonID ? (ship[0].rateOfExchange + shipDefault[0].rateOfExchange) / 2 : shipDefault[0].rateOfExchange;
		cart.map(s => totalQty += s.Price * s.Qty);
		
		this.setState({
			order: {
				...order,
				ShipPrice: shipPrice,
				ShipName: ship[0].name,
				ShipType: ship[0].id,
				SubTotal: totalQty
			}
		});
		
	}

	handleClick(event) {
		
		//let { ordersDetails } = this.props;
		let { orders} = this.state;
		if (event.target.id !== null) {
			let item = orders.filter(m => m.id === event.target.id);
			this.setState({
				ordersDetails: item != null ? item : []
			});
		}
	}


	handleSubmit(event) {
		event.preventDefault();
		let { order, cart } = this.state;

		this.setState({
			order: {
				...order,
				Cart: cart
			}
		});
		if (this.state.order.Email && this.state.order.MobilePhone && this.state.order.Address) {
			
			this.props.actions.placeorder(order)
			.then(() => {
				toast("Order successful!");
				this.props.actions.removeAllCart();
				this.props.actions.updateTotalCart("0");

				history.push("/home/");
				
			}).catch(() => {
							
			});
			this.setState({
				Cart: [], submitted: true
			});
		}
	}

	render() {
		const { orders, ordersDetails, user } = this.state;
		
		return (
			<>
				{user.username.length === 0 ? (
					<><Redirect to="/home" /><HomePage /><ToastContainer />
					</>
				): (
						<>
						<div className="page-top-info">
							<div className="container">
								<h4>Management Orders</h4>
								<div className="site-pagination">
									<Link to={"/home/"}>Home</Link>
								</div>
							</div>
						</div>
							<section className="product-section spad">
								<div className="container">
									<div className="row">
										<div className="col-lg-8 order-2 order-lg-1">
											<form className="checkout-form" onSubmit={this.handleSubmit}>
												
												<div className="col-lg-12">
													<div className="cart-table">
														<h3>Order Info</h3>
														<div className="cart-table-warp">
															<table>
																<thead>
																	<tr>
																		<th className="product-th">Order Date</th>
																		<th className="quy-th">Ship</th>
																		<th className="size-th">Pay</th>
																		<th className="total-th">Total</th>
																		<th className="size-th">Status</th>
																		<th className="size-th"></th>
																	</tr>
																</thead>
																<tbody>
																	{
																		orders != null && orders.length > 0 ?
																			orders.map(c =>

																				<tr key={c.orderDate}>
																					<td className="product-col">
																						
																						<div className="pc-title">
																							<p>{c.orderDate}</p>
																							
																						</div></td>
																					<td className="quy-col"><p>{c.shipPrice}</p>
																						
																					</td>
																					<td className="total-col"><p>{c.isPay === true ? "Completed" : "UnCompleted"}</p></td>
																					<td className="total-col"><p>${c.subTotal}</p></td>
																					<td className="total-col"><p>{c.shipName}</p></td>
																					<td className="total-col"><a id={c.id} onClick={this.handleClick}>Details</a></td>
																				</tr>
																			)
																			: <tr><td>Orders is empty</td></tr>
																	}
																</tbody>
															</table>
														</div>
														<div className="total-cost">
															
														</div>
													</div>
												</div><br /><br />
												<div className="cf-title">Billing Address</div>
												<div className="row address-inputs">															
													<div className="col-md-6">
														<div className={'form-group' + (!this.state.order != undefined  ? ' has-error' : '')}>
															<input type="text" placeholder="Email" name="Email" value={this.state.order.Email} onChange={this.handleChange} />
														</div>
													</div>
													<div className="col-md-6">
														<input type="text" placeholder="Phone no." name="MobilePhone" value={this.state.order.MobilePhone} onChange={this.handleChange}/>
													</div>													
													<div className="col-md-12">
														<input type="text" placeholder="Address" name="Address" value={this.state.order.Address} onChange={this.handleChange}/>
														<input type="text" placeholder="Address line 2" name="Address2" value={this.state.order.Address2} onChange={this.handleChange} />														
													</div>
												</div>
												
												<button className="site-btn submit-order-btn">Update</button>
											</form>
										</div>
										{ordersDetails[0] != null && ordersDetails[0].details != null ? 
										<div className="col-lg-4 order-1 order-lg-2">
											<div className="cart-table">
													<div className="checkout-cart">
														<h3>Order details</h3>
													<ul className="product-list">
													{
															ordersDetails[0] != null && ordersDetails[0].details != null ?
															ordersDetails[0].details.map(c =>
																<li key={c.id}>
																	<div className="pl-thumb"><img src={`/home/img/product/${c.img}`} alt="" /></div>
																	<h6>{c.name}</h6>
																	<span>{c.qty}/</span><span>${c.price}</span>
																</li>
															) : <li>orders is empty</li>
															}
													</ul>
												
													</div><div className="total-cost">
														<h6><span>Total</span><span>${ordersDetails[0].total}</span></h6>
														<h6><span>Ship</span><span>${ordersDetails[0].shipPrice }</span></h6>														
														<h6><span>Sub Total</span><span>${ordersDetails[0].subTotal}</span></h6>
											</div>
												</div>
										</div> : ""	}
									</div>
								</div>
							</section>
							
						</>
					)}
			</>
		);
	}
}
export function getShipByType(coms) {
	return coms.filter(f => f.type === "shipIn");// || f.type === "shipEx"
}
ManagementOrderPage.propTypes = {	
	cart: PropTypes.array,
	placeorder: PropTypes.func,
	actions: PropTypes.object,
	order: PropTypes.object,
	user: PropTypes.object,
	removeAllCart: PropTypes.func,
	getById: PropTypes.func,
	getAll: PropTypes.func,
	users: PropTypes.object,
	submitted: PropTypes.bool,
	updateTotalCart: PropTypes.func,
	inCart: PropTypes.number,
	commons: PropTypes.array,
	orders: PropTypes.array,
	ordersDetails: PropTypes.array
};
function mapStateToProps(state) {

	return {
		cart: state.cartReducer.cart,
		user: state.authentication.user,
		users: state.users,
		order: state.order,
		orders: state.orders,
		inCart: updateTotalCart(state.cartReducer.inCart),
		submitted: state.cartReducer.cart.length === 0 ? true : false,
		commons: state.commons.length > 0 ? getShipByType(state.commons) : []
	};
}
function mapDispatchToProps(dispatch) {
	return {
		actions: {
			placeorder: bindActionCreators(placeorder, dispatch),
			removeAllCart: bindActionCreators(removeAllCart, dispatch),
			getById: bindActionCreators(getById, dispatch),
			getAll: bindActionCreators(getAll, dispatch)
		}
	};
}
export default connect(mapStateToProps, mapDispatchToProps)(ManagementOrderPage);