import React from "react";
import { connect } from "react-redux";
import PropTypes from "prop-types";
import { bindActionCreators } from "redux";
import $ from 'jquery';
import { Link } from "react-router-dom";
import Spinner from "../Common/Spinner";
import PriceSlider from "../Common/PriceSlider";
import * as dealActions from "../../appState/actions/dealActions";
import * as commonActions from "../../appState/actions/commonActions";
import DealItem from '../Home/DealItem';
import ProductDetails from './ProductDetails';
import { CommonProperty } from './CommonProperty';

import '../Home/list.css';
class CategoryPage extends React.Component {
    
    state = {
        redirectToAddCoursePage: false,
        currentPage: 1,
        todosPerPage: 12,
        listAfterFilter: [],
        category: [],
        subcateName: "",
        subsubcateName: "",
        dealDetails: null,
        ...this.props
    };
  
    handleClick = this.handleClick.bind(this);
    componentDidMount() {

        const { dealsAllBy, commons, actions } = this.props;

        if (dealsAllBy != null && dealsAllBy.length === 0) {
            actions.getDealsAllBy().catch(error => {
                alert("Loading deals failed" + error);
            });
        }

        if (commons != null && commons.length === 0) {
            actions.getCommons().catch(error => {
                alert("Loading commons failed" + error);
            });
        }

        
    }


    handleClick(event) {
        $(".pagination > li").removeClass("active");
        event.target.className = "page-link active";
        this.setState({
            currentPage: Number(event.target.id)
        });
    }

    handleClicks(event) {
        $("input:radio[name='" + event.target.name+"']").removeAttr("checked");
        $(event.target).attr("checked", "checked");
         
    }

    handleCateClicks(event) {
        $(".category-menu li > a.active").removeClass("active");
        event.target.className = "active";
    }
    render() {

        const { currentPage, todosPerPage} = this.state;        
        const indexOfLastTodo = currentPage * todosPerPage;
        const indexOfFirstTodo = indexOfLastTodo - todosPerPage;
        const currentTodos = this.props.dealsAllBy != null && this.props.dealsAllBy.length > 0 && this.props.listAfterFilter.length === 0
            ? this.props.dealsAllBy.slice(indexOfFirstTodo, indexOfLastTodo) : this.props.listAfterFilter.length > 0 ? this.props.listAfterFilter.slice(indexOfFirstTodo, indexOfLastTodo) : [];
       
        const pageNumbers = [];
        if (this.props.dealsAllBy != null && this.props.dealsAllBy.length > 0) {
            const list = this.props.dealsAllBy != null && this.props.dealsAllBy.length > 0 && this.props.listAfterFilter.length === 0 ? this.props.dealsAllBy : this.props.listAfterFilter.length > 0 ? this.props.listAfterFilter : [];
            for (let i = 1; i <= Math.ceil(list.length / todosPerPage); i++) {
                pageNumbers.push(i);
            }
        }
        const renderPageNumbers = pageNumbers.map(number => {
            return (
                <li className="page-link"
                    key={number}
                    id={number}
                    onClick={this.handleClick}
                >  {number}
                </li>
            );
        });       
           
        return (
            <>
                {this.props.loading ? (
                    <Spinner />
                ) : (
                        <><div className="page-top-info">
                                <div className="container">
                                    <h4>Category Page</h4>
                                <div className="site-pagination">
                                    <Link to={"/category/"+ this.props.subcateName}>Home/{this.props.subcateName}/{this.props.subsubcateName}</Link></div>
                                </div>
                        </div>
                            {this.props.dealDetails === null ?
                                <section className="category-section spad">
                                    <div className="container">
                                        <div className="row">
                                            <div className="col-lg-2 order-2 order-lg-1">
                                                <div className="filter-widget">
                                                    <h2 className="fw-title">Categories</h2>
                                                    <ul className="category-menu">
                                                        {this.props.category != null && this.props.category.length > 0 ? this.props.category.map(subsub =>
                                                            <li key={subsub.id}><Link to={"/category/" + this.props.subcateName + "/" + subsub.subSubCateName} onClick={this.handleCateClicks}> {subsub.subSubCateName}</Link></li>
                                                        ) : <li> have not found!</li>}
                                                    </ul>
                                                </div>
                                                <div className="filter-widget mb-0">
                                                    <h2 className="fw-title">refine by</h2>
                                                    <div className="price-range-wrap">
                                                        <h4>Price</h4>
                                                        <div className="price-range ui-slider ui-corner-all ui-slider-horizontal ui-widget ui-widget-content" data-min="10" data-max="270">
                                                            <div className="ui-slider-range ui-corner-all ui-widget-header"></div>
                                                            <PriceSlider />
                                                        </div>
                                                        <div className="range-slider">
                                                            <div className="price-input">
                                                                <input type="text" id="minamount" /><input type="text" id="maxamount" />
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div className="filter-widget mb-0">
                                                    <h2 className="fw-title">color by</h2>
                                                    <div className="fw-color-choose">
                                                        <CommonProperty commons={this.props.commons} type="color" keyClass="cs" />
                                                    </div>
                                                </div>
                                                <div className="filter-widget mb-0">
                                                    <h2 className="fw-title">Size</h2>
                                                    <div className="fw-size-choose">
                                                        <CommonProperty commons={this.props.commons} type="size" keyClass="sc" />
                                                    </div>
                                                   
                                                </div>
                                                <div className="filter-widget mb-0">
                                                    <div className="fw-size-choose">
                                                        <CommonProperty commons={this.props.commons} type="size_num" keyClass="sc" />
                                                    </div>
                                                </div>
                                            </div>
                                            <div className="col-lg-10  order-1 order-lg-2 mb-5 mb-lg-0">
                                                <div className="row">
                                                    {currentTodos != null && currentTodos.length > 0 ?
                                                        <DealItem dealItems={currentTodos} /> : "have no found!"
                                                    }
                                                </div>
                                                <ul className="pagination">
                                                    {renderPageNumbers}
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </section>
                                : <ProductDetails details={this.props.dealDetails != null && this.props.dealDetails.length > 0 ? this.props.dealDetails[0] : []} detailsLasted={currentTodos != null && currentTodos.length > 0 ? currentTodos : []} commons={this.props.commons}/>}
                        </>
                    )}
            </>
        );
    }
}

CategoryPage.propTypes = {
    dealsAllBy: PropTypes.array.isRequired,
    actions: PropTypes.object.isRequired,
    loading: PropTypes.bool.isRequired,
    listAfterFilter: PropTypes.array.isRequired,
    category: PropTypes.array.isRequired,
    commons: PropTypes.array.isRequired,
    subcateName: PropTypes.string.isRequired,
    subsubcateName: PropTypes.string,
    dealDetails: PropTypes.array,
    ratingsDealID: PropTypes.array,
};
export function getDealID(dealsAllBy, dealID) {
    return dealsAllBy.filter(f => f.id === dealID) || null;
}

export function getDealByID(dealsAllBy, subCateID) {
    return dealsAllBy.filter(f => f.subCateName === subCateID) || null;
}

export function getDealSubByID(dealsAllBy, subSubCateID) {
    return dealsAllBy.filter(f => f.subSubCateName === subSubCateID) || null;
}

export function getDealByName(dealsAllBy, seachName) {
    const s = seachName.replace("?", "").trim();
    return dealsAllBy.filter(f => f.dealName.toLowerCase().includes(s.toLowerCase())) || dealsAllBy;
}

export function renderSubSubCateByID(subSubCate, subCateName) {
    let subcate = subSubCate.filter(f => f.subcate.subCateName.length > 0 && f.subcate.subCateName === subCateName);

    if (subcate != null && subcate.length > 0) {
        return subcate[0].subsubcates;
    }
 
}

function mapStateToProps(state, ownProps) {
    const subCateID = ownProps.match.params.subCateName != undefined ? ownProps.match.params.subCateName : "";
    const subSubCateID = ownProps.match.params.subSubCateName != undefined ? ownProps.match.params.subSubCateName : "";
    const dealID = ownProps.match.params.details != undefined ? ownProps.match.params.details : "";
    const dealName = ownProps.location.search != undefined ? ownProps.location.search : "";
    return {
        dealsAllBy:
            state.dealsAllBy.length === 0 && subCateID === 0
                ? []
                : state.dealsAllBy.map(cate => {
                    return {
                        ...cate
                    };
                }),
        listAfterFilter: state.dealsAllBy.length > 0 && subCateID.length > 0 && subSubCateID.length === 0 ? getDealByID(state.dealsAllBy, subCateID) : state.dealsAllBy.length > 0 && subSubCateID.length > 0 ? getDealSubByID(state.dealsAllBy, subSubCateID) : dealName.length > 0 ? getDealByName(state.dealsAll, dealName) : state.dealsAllBy,
        category: state.categorys != null && state.categorys.subSubCategoryViewModel.length > 0 && subCateID.length > 0 ? renderSubSubCateByID(state.categorys.subSubCategoryViewModel, subCateID) : state.categorys,
        subcateName: ownProps.match.params.subCateName,
        subsubcateName: ownProps.match.params.subSubCateName,
        commons:
            state.commons.length === 0
                ? []
                : state.commons.map(com => {
                    return {
                        ...com
                    };
                }),
        dealDetails: dealID != undefined && dealID.length > 0 && dealName.length === 0 ? getDealID(state.dealsAllBy, dealID) : null,
        loading: state.apiCallsInProgress > 0 
    };
}

function mapDispatchToProps(dispatch) {
    return {
        actions: {
            getDealsAllBy: bindActionCreators(dealActions.getDealsAllBy, dispatch),
            getCommons: bindActionCreators(commonActions.getCommons, dispatch)
        }
    };
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(CategoryPage);

