import * as types from "./actionTypes";
import * as ratingApi from "../../api/ratingApi";
import { beginApiCall, apiCallError } from "./apiStatusActions";

export function getRatingsSuccess(ratings) {
    return { type: types.GET_RATING_SUCCESS, ratings };
}

export function getRatingsByIDSuccess(ratings) {
    return { type: types.GET_RATING_ByID_SUCCESS, ratings };
}

export function saveRatingsSuccess(rating) {
    return { type: types.SAVE_RATING_SUCCESS, rating };
}

export function getRatings() {
  return function(dispatch) {
    dispatch(beginApiCall());
      return ratingApi
          .getRatings()
          .then(ratings => {
              dispatch(getRatingsSuccess(ratings));
      })
      .catch(error => {
        dispatch(apiCallError(error));
        throw error;
      });
  };
}

export function getRatingsByID(Id) {
    return function (dispatch) {
        dispatch(beginApiCall());
        return ratingApi
            .getRatingsByID(Id)
            .then(ratings => {
                dispatch(getRatingsByIDSuccess(ratings));
            })
            .catch(error => {
                dispatch(apiCallError(error));
                throw error;
            });
    };
}

export function saveRating(rating) {
    return function (dispatch) {
        dispatch(beginApiCall());
        return ratingApi
            .saveRating(rating)
            .then(rating => {
                dispatch(saveRatingsSuccess(rating));
            })
            .catch(error => {
                dispatch(apiCallError(error));
                throw error;
            });
    };
}
